* {
  h1 {
    text-align: center;
    @media (max-width: 420px) {
      font-size: 1.5rem;
    }
  }
}
.container {
  .projectsOpen {
    @media (max-width: 600px) {
      margin-top: 7rem;
    }
    h1 {
      margin-bottom: 1rem;
    }
    .projectCards {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      row-gap: 2rem;
      column-gap: 4rem;
      width: 80%;
      margin: auto;
      @media (max-width: 600px) {
        grid-template-columns: 1fr;
        row-gap: 1rem;
        column-gap: 1rem;
        width: 100%;
      }
      .ProjectCard {
        // max-width: 200px;

        @media (max-width: 420px) {
          margin-top: 2rem;
          width: 90%;
        }
      }
    }
  }
}
